.header {
    background-color: transparent;
    color: $pri-white;
    // border-bottom: 1px solid $pri-blue
    z-index: $level-10;
    width: 100%;
    height: 45px;
    position: fixed;
    line-height: 44px;

    @include at-medium {
 }        // border-bottom: 2px solid $pri-blue

    &__navbar {
        // font-size: 1rem
        // font-weight: 300
        // align-items: center

        &--logo {
            width: 150px;
            display: flex;
            align-items: center;

            &-container {
                display: block;
                width: 45px;
                text-decoration: none; }

            &-word {
                font-family: $sec-font;
                text-decoration: none;
                display: block;
                margin-left: 10px;
                font-size: 1.2rem;
                font-weight: 800;
                color: $pri-white; }

            &-word:visited {
                color: $pri-white; } }

        &--toggle {
            display: none; } //enable after tests

        &--toggle-bar::before {
            content: '';
            position: absolute;
            top: -6px;
            width: 20px;
            height: 2px;
            background-color: $pri-white; }

        &--toggle-bar {
            display: inline-block;
            position: absolute;
            top: 20px;
            right: calc(9.375%); //30px (fullWidth-containerWidth)/2
            width: 20px;
            height: 2px;
            background-color: $pri-white; }

        &--toggle-bar::after {
            content: '';
            position: absolute;
            top: 7px;
            width: 20px;
            height: 2px;
            background-color: $pri-white; }

        &--navigation-container {
            list-style-type: none;
            background-color: $pri-blue;
            height: 0px;
            transition: all 0.5s;
            overflow: hidden;
            // border-right: 1px solid $pri-light
            // border-bottom: 1px solid $pri-light
 }            // border-left: 1px solid $pri-light

        // &--toggle:checked + .header__navbar--navigation-container
        //     opacity: 1
        //     height: 190px
        //     transition: all 0.5s

        &--link-container {
            cursor: pointer;
            margin: 0 auto;
            text-align: center;
            line-height: 24px;

            &:first-child {
                padding-top: 10px; } //Must be same with next
            &:last-child {
                padding-bottom: 10px; } } //Must be same with previous

        &--link {
            text-decoration: none;
            display: block;
            padding: 8px;
            color: $pri-white; }

        // &--link:hover
        //     background-color: $pri-blue
        //     color: $pri-light
        //  // transition: all 0.3s

        &--link:visited {
            color: $pri-light; } } }

.is-open {
    opacity: 1;
    height: 260px;
    transition: all 0.5s; }

@include at-medium {
    .header {
        // height: 62px
        // line-height: 62px
        position: relative;
        margin-top: 2.4%;

        &__navbar {
            // font-size: 1.125em
            // font-weight: 100
            display: flex;
            justify-content: space-between;
            align-items: center;

            &--logo {
                width: 200px;
                display: flex;

                &-container {
                    width: 40px; }

                &-word {
                    font-family: $sec-font;
                    font-size: 24px;
                    font-weight: 800; } }

            &--navigation-container {
                display: flex;
                flex-direction: row;
                justify-content: flex-end;
                align-items: center;
                list-style-type: none;
                background-color: transparent;
                height: auto;
                opacity: 1; }

            &--link {
                text-decoration: none;
                font-family: $sec-font;
                font-size: 18px;
                padding: 0;
                border-bottom-width: 2px;
                border-bottom-style: solid;
                border-bottom-color: transparent;
                transition: border-bottom-color 0.6s ease; }

            &--link:hover {
                // background-color: $pri-light
                border-bottom-width: 2px;
                border-bottom-style: solid;
                border-bottom-color: $pri-green;
                color: $pri-light; }


            &--link:visited {
                color: $pri-white; }


            &--link-container {
                margin-left: 39px;
                line-height: unset;

                &:first-child {
                    padding-top: 0; } //Must be same with next
                &:last-child {
                    padding-bottom: 0; } } //Must be same with previous

            &--toggle,
            &--toggle-label {
                display: none; }

            &--toggle-bar {
                position: absolute;
                top: 22px;
                right: 30px;
                width: 18px;
                height: 2px;
                background-color: $pri-blue; } } } }
