.large-hero {
    position: relative;
    background-color: $pri-black;
    overflow: hidden;
    height: 100vh;

    &:before {
        content: ' ';
        display: block;
        position: absolute;
        left: 0;
        top: 0;
        width: 100%;
        height: 100%;
        z-index: $level-1;
        opacity: 0.2;
        background-image: url('../../src/img/Path 1@2x.png');
        background-repeat: no-repeat;
        background-size: cover; }

    &__carousel {

        &-container {
            position: relative;
            max-width: 70%;
            margin: 0 auto;
            z-index: $level-2;
            top: 22%;
 }            // margin-bottom: 100px

        &-content {
            text-align: center;
            display: none; }

        &--heading {
            color: $pri-white;
            font-family: $sec-font;
            font-size: 72px;
            font-weight: 800; }

        &--paragraph {
            color: $pri-white;
            font-family: $sec-font;
            font-size: 24px;
            font-weight: 800;
            margin-top: 22px;
            margin-bottom: 55px; }

        &--button {
            background-color: $pri-green;
            color: $pri-white;
            padding: 20px 28px;
            font-family: $pri-font;
            font-size: 24;
            font-weight: 500;
            outline: none;
            border-radius: 40px; }

        &-buttons {
            text-align: center;
            position: absolute;
            bottom: 5%;
            left: 0;
            right: 0;
            margin-left: auto;
            margin-right: auto;
            z-index: $level-2; } } }

.dot {
    cursor: pointer;
    height: 21px;
    width: 21px;
    margin: 0 2px;
    border: 1px solid $pri-white;
    background-color: transparent;
    border-radius: 50%;
    display: inline-block;
    transition: background-color 0.6s ease;

    &:hover,&.selected {
        background-color: $pri-green; } }

.active {
    display: block; }
